const Interests = () => {

    if (typeof window === `undefined`) {
        return(<></>);
    }

    const mobileScreenWidth = 900
    const [isMobile, setIsMobile] = useState(window.innerWidth < mobileScreenWidth)

    useEffect(() => {
        function handleWindowResize() {
            setIsMobile(window.innerWidth < mobileScreenWidth)
        }
        window.addEventListener('resize', handleWindowResize);
        return () => {
            window.removeEventListener('resize', handleWindowResize);
        };
    }, []);

    return (
        <>
            <Typography variant={'h4'}>
                Interests
            </Typography>
            <Grid2 container spacing={3}>
                <Grid2 xs={isMobile? 12 : 4}>
                    <img src={SkiTouringImage} style={{maxWidth: '100%'}} />
                </Grid2>
                <Grid2 xs={isMobile? 12 : 8}>
                    <Typography variant={'body1'}>
                        Being raised in Whistler, BC, Canada, gave me a profound appreciation for the mountains.
                        I grew up with skiing, mountain biking, hiking, and skateboarding.
                        Throughout my teenage years, I was a competitive freestyle skier until finding my love for
                        the backcountry where I started ski-touring and mountaineering. Since then, I have been experimenting with new ways to
                        explore the mountains in my local area through rock climbing, snowmobiling and snowboarding.
                    </Typography>
                </Grid2>
                <Grid2 xs={isMobile? 12 : 8}>
                    <Typography variant={'body1'}>
                        When it is too wet to climb and too dry to ski, you may find me learning a new song on the
                        guitar, tinkering with music production, surfing the coastal waters of British Columbia, or working on a construction project.
                    </Typography>
                </Grid2>
                <Grid2 xs={isMobile? 12 : 4}>
                    <img src={ChekImage} style={{maxWidth: '100%'}} />
                </Grid2>
            </Grid2>
        </>
    );

}

